<template>
  <div>
    <loading-component :value="loading" />
    <v-snackbar v-model="snackbar" :timeout="timeout" :color="snackbarColor" top >
      {{ message }}
    </v-snackbar>
    <div v-if="this.currentQuestion && !loading" justify="center " class="mb-2">
      <v-card class="blue darken-0" style="margin: 20px auto" width="500px">
        <v-card-title class="title">
          <span class="headline --text-white">Question{{ counter }} :</span>
        </v-card-title>
        <v-card-text class="white text--primary">
          <p class="headline">{{ currentQuestion.content }}</p>
          <v-text-field
            v-model="data.note"
            label="Add a Note"
          ></v-text-field>
          <v-btn small @click="nextQuestion(true)">yes</v-btn>
          <v-btn small @click="nextQuestion(false)">no</v-btn>
        </v-card-text>
      </v-card>
    </div>
    <div v-else-if="!loading">
      <p class="headline">Thank you for your time.</p>
      <v-btn class="primary">
        <router-link
          tag="div"
          style="decoration: none"
          :to="{ name: 'Surveys' }"
        >
          Take another one
        </router-link>
      </v-btn>
    </div>
  </div>
</template>
<script>
import LoadingComponent from "@/components/LoadingComponent";

export default {
  name: "TakeSurvey",
  components: { LoadingComponent },
  mounted() {
    this.loading = true;
    this.axios
      .post(this.api.ACTIONS.TAKE_SURVEY, this.data)
      .then((res) => {
        this.loading = false;
        this.currentQuestion = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    nextQuestion(answer) {
      this.counter++;
      this.loading = true;
      this.data.answer = answer;
      this.data.question_id = this.currentQuestion.id;
      this.axios
        .post(this.api.ACTIONS.NEXT_QUESTION, this.data)
        .then((res) => {
          this.currentQuestion = res.data.data;
          this.loading = false;
          if (this.currentQuestion == null) {
            this.message = "Survey has ended";

            this.snackbar = true;
          }
        })
        .catch((err) => console.log(err));
    },
  },
  data() {
    return {
      loading: false,
      counter: 1,
      timeout: 2000,
      snackbar: false,
      snackbarColor: "primary",
      message: "",
      currentQuestion: {
        content: "",
      },
      data: {
        survey_id: this.$route.params.id,
        agent_id: this.$store.state.agent_id,
      },
    };
  },
};
</script>
<style lang="css" scoped>
</style>
